import CloudDownloadIcon from '@mui/icons-material/CloudDownload';

import './index.css';

const FileDownload = () => {
  const throttleFn = (fn, delay) => {
    let invokeFn = true;;
    return function(...args) {
      console.log("throttle function called")
      if(invokeFn) {
        fn.apply(null, args);
        invokeFn = false;
        setTimeout(() => {
          invokeFn = true;
        }, delay);
      }
    }
  };

  // const handleDownload = () => {
  //   const filePath = `${process.env.PUBLIC_URL}/Jayanth_R_Resume.pdf`;
  //   const link = document.createElement('a');
  //   link.href = filePath;
  //   link.download = 'Jayanth_R_Resume.pdf';
  //   document.body.appendChild(link);
  //   link.click();
  //   document.body.removeChild(link);
  // };

  const handleDownload = async () => {
    const filePath = `${process.env.PUBLIC_URL}/Jayanth_R_Resume.pdf`;

    try {
      // Fetch the file as a blob
      const response = await fetch(filePath);
      if (!response.ok) {
        throw new Error('Network response was not ok');
      }
      const blob = await response.blob();

      // Create a link element
      const link = document.createElement('a');
      link.href = URL.createObjectURL(blob);
      link.download = 'Jayanth_R_Resume.pdf'; // Suggest the file name for download

      // Append the link to the body
      document.body.appendChild(link);

      // Programmatically click the link to trigger the download
      link.click();

      // Remove the link from the document
      document.body.removeChild(link);

      // Revoke the object URL to free up memory
      URL.revokeObjectURL(link.href);
    } catch (error) {
      console.error('Error downloading the file:', error);
    }
  }

  const throttledDownload = throttleFn(handleDownload, 500);

  return (
    <div className="resume-download" onClick={throttledDownload}>
      <span className="resume-text">DOWNLOAD </span>
      <CloudDownloadIcon />
    </div>
  );
};

export default FileDownload;
